.bulk-status-editor {
  margin: 20px auto;
  padding: 28px;
  background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
  border-radius: 20px;
  max-width: 900px;
  box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
              0 0 20px rgba(33, 165, 185, 0.2),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(33, 165, 185, 0.2);
}

.bulk-editor-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid rgba(33, 165, 185, 0.2);
}

.bulk-editor-header h3 {
  margin: 0;
  color: rgba(99, 102, 241, 0.3);
  font-size: 24px;
  font-weight: 600;
  background: transparent;
}

.close-btn {
  background: none;
  border: none;
  font-size: 32px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.7);
  padding: 5px 10px;
  line-height: 1;
  transition: all 0.3s ease;
  border-radius: 8px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-btn:hover {
  color: rgba(255, 255, 255, 1);
  background: rgba(255, 255, 255, 0.1);
}

.bulk-editor-stats {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
  margin-bottom: 24px;
  padding: 16px;
  background: rgba(33, 165, 185, 0.1);
  border-radius: 12px;
}

.stat-item {
  display: flex;
  gap: 8px;
  color: rgba(255, 255, 255, 0.85);
  font-size: 14px;
}

.stat-item strong {
  color: rgba(33, 165, 185, 1);
  font-weight: 600;
}

.bulk-editor-controls {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.control-group {
  flex: 1;
  min-width: 200px;
}

.control-group label {
  display: block;
  margin-bottom: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 14px;
  font-weight: 500;
}

.control-group select {
  width: 100%;
  padding: 12px 16px;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.95);
  background: rgba(26, 0, 55, 0.4);
  border: 2px solid rgba(33, 165, 185, 0.3);
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
  box-sizing: border-box;
  font-family: inherit;
  cursor: pointer;
}

.control-group select:focus {
  border-color: rgba(33, 165, 185, 0.6);
  background: rgba(26, 0, 55, 0.5);
  box-shadow: 0 0 20px rgba(33, 165, 185, 0.3);
}

.bulk-editor-actions {
  display: flex;
  gap: 12px;
  align-items: center;
  margin-bottom: 24px;
  flex-wrap: wrap;
}

.btn-select-all,
.btn-apply {
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  border: 2px solid;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  letter-spacing: 0.5px;
}

.btn-select-all {
  background: rgba(26, 0, 55, 0.3);
  color: rgba(255, 255, 255, 0.9);
  border-color: rgba(255, 255, 255, 0.3);
}

.btn-select-all:hover {
  background: rgba(26, 0, 55, 0.5);
  border-color: rgba(255, 255, 255, 0.5);
}

.btn-apply {
  flex: 1;
  background: linear-gradient(60deg, rgba(33, 165, 185, 0.3) 0%, rgba(33, 165, 185, 0.5) 100%);
  color: white;
  border-color: rgba(33, 165, 185, 0.4);
}

.btn-apply:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(33, 165, 185, 0.4);
  border-color: rgba(33, 165, 185, 0.6);
}

.btn-apply:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.help-text {
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
  font-style: italic;
}

.bulk-editor-list {
  max-height: 400px;
  overflow-y: auto;
  padding: 12px;
  background: rgba(26, 0, 55, 0.2);
  border-radius: 12px;
  border: 1px solid rgba(33, 165, 185, 0.2);
}

.bulk-editor-list::-webkit-scrollbar {
  width: 8px;
}

.bulk-editor-list::-webkit-scrollbar-track {
  background: rgba(26, 0, 55, 0.3);
  border-radius: 4px;
}

.bulk-editor-list::-webkit-scrollbar-thumb {
  background: rgba(33, 165, 185, 0.5);
  border-radius: 4px;
}

.bulk-editor-list::-webkit-scrollbar-thumb:hover {
  background: rgba(33, 165, 185, 0.7);
}

.tech-select-item {
  padding: 12px 16px;
  margin-bottom: 8px;
  background: rgba(26, 0, 55, 0.3);
  border: 2px solid rgba(33, 165, 185, 0.2);
  border-radius: 12px;
  transition: all 0.3s ease;
}

.tech-select-item:hover {
  background: rgba(26, 0, 55, 0.5);
  border-color: rgba(33, 165, 185, 0.4);
}

.tech-select-item.selected {
  background: rgba(33, 165, 185, 0.2);
  border-color: rgba(33, 165, 185, 0.6);
  box-shadow: 0 0 15px rgba(33, 165, 185, 0.3);
}

.tech-checkbox-label {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  width: 100%;
}

.tech-checkbox-label input[type="checkbox"] {
  width: 20px;
  height: 20px;
  cursor: pointer;
  accent-color: rgba(33, 165, 185, 0.8);
}

.tech-title {
  flex: 1;
  color: rgba(255, 255, 255, 0.9);
  font-size: 16px;
  font-weight: 500;
}

.tech-status-badge {
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.tech-status-badge.not-started {
  background: rgba(255, 107, 107, 0.3);
  color: rgba(255, 107, 107, 1);
  border: 1px solid rgba(255, 107, 107, 0.5);
}

.tech-status-badge.in-progress {
  background: rgba(33, 165, 185, 0.3);
  color: rgba(33, 165, 185, 1);
  border: 1px solid rgba(33, 165, 185, 0.5);
}

.tech-status-badge.completed {
  background: rgba(117, 130, 40, 0.3);
  color: rgba(117, 130, 40, 1);
  border: 1px solid rgba(117, 130, 40, 0.5);
}

.no-techs {
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
  font-style: italic;
  padding: 40px;
}

@media (max-width: 768px) {
  .bulk-status-editor {
    padding: 20px;
  }

  .bulk-editor-stats {
    flex-direction: column;
    gap: 8px;
  }

  .bulk-editor-controls {
    flex-direction: column;
  }

  .bulk-editor-actions {
    flex-direction: column;
  }

  .btn-apply {
    width: 100%;
  }
}

