.main-navigation {
  display: flex;
  justify-content: center;
  align-items: left;
  padding: 20px 40px;
  background: linear-gradient(135deg, var(--navbar-color) 0%, rgba(59, 130, 246, 0.05) 100%);
  border-radius: 24px;
  margin: 20px auto;
  max-width: 880px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1),
              0 0 0 1px var(--accent-border),
              0 4px 20px var(--accent-shadow);
  border: 1px solid var(--accent-border);
  backdrop-filter: blur(10px);
}

.nav-brand h2 {
  text-align: center;
  font-size: 28px;
  font-weight: bold;
  color: var(--text-color);
  text-decoration: none;
}

.nav-brand a {
  text-decoration: none;
}

.nav-menu {
  display: flex;
  list-style: none;
  gap: 25px;
  margin: 2px;
  padding: 2px;
  align-items: center;
}

.nav-menu li {
  margin: 0;
}

.nav-menu a {
  color: var(--text-color);
  text-decoration: none;
  font-size: 18px;
  font-weight: 500;
  padding: 10px 20px;
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.nav-menu a:hover {
  color: #ffffff;
  background: rgba(33, 165, 185, 0.6);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(33, 165, 185, 0.3);
}

.nav-menu a.active {
  color: #ffffff;
  background: rgba(33, 165, 185, 0.3);
  box-shadow: 0 4px 15px rgba(33, 165, 185, 0.3);
}

.user-info {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-left: 20px;
}

.username {
  color: var(--text-color);
  font-size: 16px;
  font-weight: 500;
}

.logout-btn {
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  border: 2px solid transparent;
  border-radius: 20px;
  background: linear-gradient(135deg, rgba(239, 68, 68, 0.8) 0%, rgba(220, 38, 38, 0.9) 100%);
  color: white;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  letter-spacing: 0.5px;
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3),
              inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.logout-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(239, 68, 68, 0.5),
              0 0 15px rgba(239, 68, 68, 0.3);
  background: linear-gradient(135deg, rgba(220, 38, 38, 0.9) 0%, rgba(239, 68, 68, 0.8) 100%);
}

.logout-btn:active {
  transform: translateY(0) scale(0.98);
}

@media (max-width: 768px) {
  .main-navigation {
    flex-direction: column;
    gap: 20px;
    padding: 20px;
  }

  .nav-menu {
    flex-direction: column;
    gap: 15px;
    width: 100%;
  }

  .nav-menu a {
    display: block;
    width: 100%;
    text-align: center;
  }

  .user-info {
    flex-direction: column;
    margin-left: 0;
    width: 100%;
  }

  .logout-btn {
    width: 100%;
  }
}

