.deadline-form {
  margin: 20px auto;
  padding: 28px;
  background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
  border-radius: 20px;
  max-width: 600px;
  box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
              0 0 20px rgba(33, 165, 185, 0.2),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(33, 165, 185, 0.2);
}

.deadline-form-modal {
  padding: 0;
}

.deadline-form h3 {
  margin-top: 0;
  margin-bottom: 20px;
  color: rgba(99, 102, 241, 0.3);
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  background: transparent;
  background-clip: text;
}

.deadline-form-tech-info {
  padding: 16px;
  background: rgba(33, 165, 185, 0.1);
  border-radius: 12px;
  border-left: 3px solid rgba(33, 165, 185, 0.5);
  margin-bottom: 20px;
}

.deadline-form-tech-info p {
  margin: 8px 0;
  color: rgba(255, 255, 255, 0.85);
}

.deadline-form .form-group,
.deadline-form-modal .form-group {
  margin-bottom: 20px;
}

.deadline-form label,
.deadline-form-modal label {
  display: block;
  margin-bottom: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 16px;
  font-weight: 500;
}

.deadline-form label.required::after,
.deadline-form-modal label.required::after {
  content: ' *';
  color: rgba(255, 107, 107, 0.8);
}

.deadline-form input[type="date"] {
  width: 100%;
  padding: 14px 18px;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.95);
  background: rgba(26, 0, 55, 0.4);
  border: 2px solid rgba(33, 165, 185, 0.3);
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
  box-sizing: border-box;
  font-family: inherit;
}

.deadline-form input[type="date"]:focus,
.deadline-form-modal input[type="date"]:focus {
  border-color: rgba(33, 165, 185, 0.6);
  background: rgba(26, 0, 55, 0.5);
  box-shadow: 0 0 20px rgba(33, 165, 185, 0.3),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.deadline-form input[type="date"].error,
.deadline-form-modal input[type="date"].error {
  border-color: rgba(255, 107, 107, 0.6);
  background: rgba(255, 107, 107, 0.1);
}

.deadline-form .error-message,
.deadline-form-modal .error-message {
  display: block;
  margin-top: 8px;
  color: rgba(255, 107, 107, 0.9);
  font-size: 14px;
  font-weight: 500;
}

.deadline-form .help-text,
.deadline-form-modal .help-text {
  margin-top: 8px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
  line-height: 1.6;
}

.deadline-form-modal .help-text {
  margin-top: 8px;
}

.deadline-preview {
  padding: 16px;
  background: rgba(117, 130, 40, 0.2);
  border-radius: 12px;
  border-left: 3px solid rgba(117, 130, 40, 0.6);
  margin-bottom: 20px;
}

.deadline-preview p {
  margin: 0;
  color: rgba(255, 255, 255, 0.9);
}

.deadline-preview strong {
  color: rgba(117, 130, 40, 1);
}

.form-validation-info {
  padding: 12px;
  background: rgba(255, 107, 107, 0.2);
  border-radius: 8px;
  border-left: 3px solid rgba(255, 107, 107, 0.6);
  margin-bottom: 20px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 14px;
}

.deadline-form .form-actions {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.deadline-form .btn-primary,
.deadline-form .btn-secondary,
.deadline-form .btn-danger,
.deadline-form-modal .btn-primary,
.deadline-form-modal .btn-secondary,
.deadline-form-modal .btn-danger {
  flex: 1;
  min-width: 120px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  border: 2px solid;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  letter-spacing: 0.5px;
}

.deadline-form .btn-primary,
.deadline-form-modal .btn-primary {
  background: linear-gradient(60deg, rgba(33, 165, 185, 0.3) 0%, rgba(33, 165, 185, 0.5) 100%);
  color: white;
  border-color: rgba(33, 165, 185, 0.4);
}

.deadline-form .btn-primary:hover:not(:disabled),
.deadline-form-modal .btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(33, 165, 185, 0.4);
  border-color: rgba(33, 165, 185, 0.6);
}

.deadline-form .btn-primary:disabled,
.deadline-form-modal .btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.deadline-form .btn-secondary,
.deadline-form-modal .btn-secondary {
  background: rgba(26, 0, 55, 0.3);
  color: rgba(255, 255, 255, 0.9);
  border-color: rgba(255, 255, 255, 0.3);
}

.deadline-form .btn-secondary:hover,
.deadline-form-modal .btn-secondary:hover {
  background: rgba(26, 0, 55, 0.5);
  border-color: rgba(255, 255, 255, 0.5);
}

.deadline-form .btn-danger {
  background: linear-gradient(60deg, rgba(255, 107, 107, 0.3) 0%, rgba(255, 107, 107, 0.5) 100%);
  color: white;
  border-color: rgba(255, 107, 107, 0.4);
}

.deadline-form .btn-danger:hover,
.deadline-form-modal .btn-danger:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(255, 107, 107, 0.4);
  border-color: rgba(255, 107, 107, 0.6);
}

@media (max-width: 768px) {
  .deadline-form {
    padding: 20px;
  }

  .deadline-form .form-actions {
    flex-direction: column;
  }

  .deadline-form .btn-primary,
  .deadline-form .btn-secondary,
  .deadline-form .btn-danger {
    width: 100%;
  }
}

