.login-page {
  min-height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.login-container {
  width: 100%;
  max-width: 450px;
  padding: 40px;
  background: linear-gradient(135deg, var(--box-color) 0%, rgba(59, 130, 246, 0.05) 100%);
  border-radius: 24px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1),
              0 0 0 1px var(--accent-border),
              0 4px 20px var(--accent-shadow);
  border: 1px solid var(--accent-border);
  backdrop-filter: blur(10px);
}

.login-container h1 {
  margin: 0 0 30px 0;
  text-align: center;
  font-size: 32px;
  font-weight: bold;
  color: rgba(99, 102, 241, 0.3);
  background: transparent;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.login-form .form-group {
  margin-bottom: 24px;
}

.login-form label {
  display: block;
  margin-bottom: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 16px;
  font-weight: 500;
}

.login-form .form-input {
  width: 100%;
  padding: 14px 18px;
  font-size: 16px;
  color: var(--text-color);
  background: var(--back-color);
  border: 2px solid var(--accent-border);
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
  box-sizing: border-box;
  font-family: inherit;
}

.login-form .form-input::placeholder {
  color: var(--text-color);
  opacity: 0.5;
}

.login-form .form-input:focus {
  border-color: var(--accent-border-focus);
  background: var(--box-color);
  box-shadow: 0 0 0 3px var(--accent-shadow),
              0 0 20px var(--accent-shadow-hover);
  transform: translateY(-1px);
}

.login-btn {
  width: 100%;
  padding: 14px 28px;
  font-size: 16px;
  font-weight: 600;
  border: 2px solid transparent;
  border-radius: 30px;
  background: linear-gradient(135deg, var(--accent-text) 0%, var(--accent-secondary) 100%);
  color: white;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  letter-spacing: 0.5px;
  box-shadow: 0 4px 15px var(--accent-shadow-hover),
              inset 0 1px 0 rgba(255, 255, 255, 0.2);
  margin-top: 10px;
}

.login-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px var(--accent-shadow-hover),
              0 0 20px var(--accent-shadow);
  background: linear-gradient(135deg, var(--accent-secondary) 0%, var(--accent-text) 100%);
}

.login-btn:active {
  transform: translateY(0) scale(0.98);
}

.login-hint {
  margin-top: 20px;
  text-align: center;
  padding: 12px;
  background: linear-gradient(90deg, var(--accent-bg) 0%, rgba(139, 92, 246, 0.05) 100%);
  border-radius: 12px;
  border-left: 3px solid var(--accent-text);
  backdrop-filter: blur(5px);
}

.login-hint p {
  margin: 0;
  color: var(--text-color);
  opacity: 0.8;
  font-size: 14px;
  font-style: italic;
}

@media (max-width: 768px) {
  .login-container {
    padding: 30px 20px;
  }

  .login-container h1 {
    font-size: 28px;
  }
}
