.settings-page {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.settings-page h1 {
  text-align: center;
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 40px;
  color: rgba(99, 102, 241, 0.3);
  background: transparent;
}

.settings-section {
  margin-bottom: 40px;
  padding: 28px;
  background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
              0 0 20px rgba(33, 165, 185, 0.2),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(33, 165, 185, 0.2);
}

.settings-section h2 {
  margin: 0 0 24px 0;
  color: rgba(99, 102, 241, 0.3);
  font-size: 24px;
  font-weight: 600;
  border-bottom: 2px solid rgba(33, 165, 185, 0.3);
  padding-bottom: 12px;
}

.setting-item {
  margin-bottom: 24px;
}

.setting-item label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: rgba(255, 255, 255, 0.9);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}

.setting-item label span:first-child {
  flex: 1;
}

.setting-select {
  padding: 10px 16px;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.95);
  background: rgba(26, 0, 55, 0.4);
  border: 2px solid rgba(33, 165, 185, 0.3);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  outline: none;
}

.setting-select:hover {
  border-color: rgba(33, 165, 185, 0.6);
  background: rgba(26, 0, 55, 0.5);
}

.setting-select:focus {
  border-color: rgba(33, 165, 185, 0.6);
  box-shadow: 0 0 20px rgba(33, 165, 185, 0.3);
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
}

.setting-checkbox {
  width: 20px;
  height: 20px;
  cursor: pointer;
  accent-color: rgba(33, 165, 185, 0.8);
}

.setting-description {
  margin: 8px 0 0 32px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
  font-style: italic;
}

.data-actions {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 16px;
}

.action-btn {
  padding: 14px 28px;
  font-size: 16px;
  font-weight: 500;
  border: 2px solid rgba(33, 165, 185, 0.4);
  border-radius: 12px;
  background: linear-gradient(60deg, rgba(33, 165, 185, 0.3) 0%, rgba(33, 165, 185, 0.5) 100%);
  color: white;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  letter-spacing: 0.5px;
  text-align: center;
}

.action-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 40px rgba(33, 165, 185, 0.5);
  border-color: rgba(33, 165, 185, 0.6);
}

.export-btn {
  background: linear-gradient(60deg, rgba(33, 165, 185, 0.3) 0%, rgba(33, 165, 185, 0.5) 100%);
}

.import-btn {
  background: linear-gradient(60deg, rgba(117, 130, 40, 0.3) 0%, rgba(117, 130, 40, 0.5) 100%);
  border-color: rgba(117, 130, 40, 0.4);
}

.import-btn:hover {
  box-shadow: 0 15px 40px rgba(117, 130, 40, 0.5);
  border-color: rgba(117, 130, 40, 0.6);
}

.danger-btn {
  background: linear-gradient(60deg, rgba(255, 107, 107, 0.3) 0%, rgba(255, 107, 107, 0.5) 100%);
  border-color: rgba(255, 107, 107, 0.4);
}

.danger-btn:hover {
  box-shadow: 0 15px 40px rgba(255, 107, 107, 0.5);
  border-color: rgba(255, 107, 107, 0.6);
}

.setting-warning {
  color: rgba(255, 107, 107, 0.9);
  font-size: 14px;
  margin: 0;
  padding: 12px;
  background: rgba(255, 107, 107, 0.1);
  border-radius: 8px;
  border-left: 3px solid rgba(255, 107, 107, 0.5);
}

.info-item {
  padding: 12px;
  margin-bottom: 12px;
  color: rgba(255, 255, 255, 0.9);
  background: rgba(26, 0, 55, 0.2);
  border-radius: 8px;
  border-left: 3px solid rgba(33, 165, 185, 0.5);
}

.info-item strong {
  color: rgba(255, 255, 255, 1);
  margin-right: 8px;
}

@media (max-width: 768px) {
  .settings-page {
    padding: 10px;
  }

  .settings-section {
    padding: 20px;
  }

  .setting-item label {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .setting-select {
    width: 100%;
  }
}

