html {
    --text-color: rgb(15, 23, 42);
    --box-color: rgba(255, 255, 255, 0.95);
    --back-color: rgb(240, 249, 255);
    --navbar-color: rgba(255, 255, 255, 0.9);
    --accent-border: rgba(59, 130, 246, 0.3);
    --accent-border-hover: rgba(59, 130, 246, 0.6);
    --accent-border-focus: rgba(139, 92, 246, 0.7);
    --accent-shadow: rgba(59, 130, 246, 0.2);
    --accent-shadow-hover: rgba(139, 92, 246, 0.4);
    --accent-bg: rgba(59, 130, 246, 0.1);
    --accent-text: rgb(59, 130, 246);
    --accent-secondary: rgb(139, 92, 246);
    --success-color: rgb(16, 185, 129);
    --warning-color: rgb(251, 191, 36);
    --error-color: rgb(239, 68, 68);
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: var(--back-color);
    color: var(--text-color);
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
    background: var(--back-color);
    color: var(--text-color);
}

p {
    font-family: "serif";
    font-size: 20px;
    color: var(--text-color);
}
