.quick-actions {
    margin: 20px auto;
    padding: 28px;
    background: linear-gradient(135deg, var(--box-color) 0%, rgba(59, 130, 246, 0.05) 100%);
    border-radius: 24px;
    max-width: 800px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1),
                0 0 0 1px var(--accent-border),
                0 4px 20px var(--accent-shadow);
    border: 1px solid var(--accent-border);
    backdrop-filter: blur(10px);
}

.quick-actions h3 {
    margin-top: 0;
    margin-bottom: 20px;
    color: rgba(99, 102, 241, 0.3);
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    background: transparent;
}

.actions-buttons {
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    justify-content: center;
}

.action-btn {
    padding: 14px 28px;
    font-size: 14px;
    font-weight: 500;
    border: 2px solid transparent;
    border-radius: 30px;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    flex: 1;
    min-width: 200px;
    letter-spacing: 0.5px;
    position: relative;
    overflow: hidden;
    color: white;
    z-index: 1;
}

.action-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.3s ease;
    border-radius: 30px;
    z-index: -1;
}

.action-btn:hover {
    transform: translateY(-2px) scale(1.05);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

.action-btn:active {
    transform: translateY(-2px) scale(1.02);
}

.action-btn span,
.action-btn {
    position: relative;
    z-index: 1;
}

.complete-all {
    background: var(--success-color);
    color: white;
    border-color: transparent;
    box-shadow: 0 4px 15px rgba(16, 185, 129, 0.3);
}

.complete-all::before {
    background: rgba(5, 150, 105, 1);
}

.complete-all:hover {
    background: rgba(5, 150, 105, 1);
    border-color: transparent;
    box-shadow: 0 8px 25px rgba(16, 185, 129, 0.5),
                0 0 20px rgba(16, 185, 129, 0.3);
}

.complete-all:hover::before {
    opacity: 0;
}

.reset-all {
    background: linear-gradient(135deg, rgba(239, 68, 68, 0.9) 0%, rgba(220, 38, 38, 1) 100%);
    color: white;
    border-color: transparent;
    box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3),
                inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.reset-all::before {
    background: linear-gradient(135deg, rgba(220, 38, 38, 1) 0%, rgba(239, 68, 68, 0.9) 100%);
}

.reset-all:hover {
    background: linear-gradient(135deg, rgba(220, 38, 38, 1) 0%, rgba(239, 68, 68, 0.9) 100%);
    border-color: transparent;
    box-shadow: 0 8px 25px rgba(239, 68, 68, 0.5),
                0 0 20px rgba(239, 68, 68, 0.3);
}

.reset-all:hover::before {
    opacity: 0;
}

.random-next {
    background: var(--accent-text);
    color: white;
    border-color: transparent;
    box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
}

.random-next::before {
    background: var(--accent-secondary);
}

.random-next:hover {
    background: var(--accent-secondary);
    border-color: transparent;
    box-shadow: 0 8px 25px rgba(139, 92, 246, 0.5),
                0 0 20px rgba(59, 130, 246, 0.3);
}

.random-next:hover::before {
    opacity: 0;
}

.export-data {
    background: var(--accent-text);
    color: white;
    border-color: transparent;
    box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
}

.export-data::before {
    background: var(--accent-secondary);
}

.export-data:hover {
    background: var(--accent-secondary);
    border-color: transparent;
    box-shadow: 0 8px 25px rgba(139, 92, 246, 0.5),
                0 0 20px rgba(59, 130, 246, 0.3);
}

.export-data:hover::before {
    opacity: 0;
}

@media (max-width: 640px) {
    .quick-actions {
        padding: 20px;
    }

    .quick-actions h3 {
        font-size: 20px;
    }

    .action-btn {
        min-width: 100%;
        padding: 12px 24px;
    }
}
