.modal-background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

.modal-window {
  background: var(--box-color);
  border-radius: 20px;
  padding: 0;
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3),
              0 0 40px var(--accent-shadow-hover),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid var(--accent-border);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid var(--accent-border);
}

.modal-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: rgba(99, 102, 241, 0.3);
}

.close-button {
  background: none;
  border: none;
  font-size: 32px;
  cursor: pointer;
  color: var(--text-color);
  opacity: 0.7;
  padding: 5px 10px;
  line-height: 1;
  transition: all 0.3s ease;
  border-radius: 8px;
}

.close-button:hover {
  opacity: 1;
  background: rgba(0, 0, 0, 0.1);
}

.modal-content {
  padding: 24px;
  color: var(--text-color);
}

.modal-content p {
  margin: 12px 0;
  line-height: 1.6;
}

.modal-content button {
  margin-top: 16px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 500;
  border: 2px solid var(--accent-border-hover);
  border-radius: 30px;
  background: linear-gradient(60deg, var(--accent-bg) 0%, var(--accent-text) 100%);
  opacity: 0.8;
  color: white;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  letter-spacing: 0.5px;
}

.modal-content button:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 40px var(--accent-shadow-hover);
  border-color: var(--accent-border-focus);
}

.modal-content button:active {
  transform: translateY(0) scale(0.98);
}

.modal-content textarea {
  width: 100%;
  padding: 14px 18px;
  font-size: 16px;
  color: var(--text-color);
  background: var(--back-color);
  border: 2px solid var(--accent-border);
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
  box-sizing: border-box;
  font-family: inherit;
  resize: vertical;
  min-height: 150px;
  margin-top: 12px;
}

.modal-content textarea::placeholder {
  color: var(--text-color);
  opacity: 0.5;
}

.modal-content textarea:focus {
  border-color: var(--accent-border-focus);
  background: var(--box-color);
  box-shadow: 0 0 20px var(--accent-shadow-hover),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.modal-content label {
  display: block;
  margin-bottom: 8px;
  color: var(--text-color);
  font-size: 16px;
  font-weight: 500;
}

.modal-description {
  padding: 12px;
  border-radius: 8px;
  background: var(--accent-bg);
  border-left: 3px solid var(--accent-text);
  margin-bottom: 20px;
  color: var(--text-color);
  opacity: 0.85;
  line-height: 1.6;
}

.deadline-section {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid var(--accent-border);
}

.deadline-info h4 {
  margin: 0 0 12px 0;
  color: rgba(99, 102, 241, 0.3);
  font-size: 18px;
  font-weight: 600;
}

.deadline-display {
  padding: 12px;
  background: rgba(117, 130, 40, 0.1);
  border-radius: 8px;
  border-left: 3px solid rgba(117, 130, 40, 0.5);
  margin-bottom: 12px;
}

.deadline-display p {
  margin: 4px 0;
  color: var(--text-color);
  opacity: 0.85;
}

.deadline-overdue {
  margin-top: 8px !important;
  font-weight: 600;
}

.no-deadline {
  color: var(--text-color);
  opacity: 0.5;
  font-style: italic;
  margin: 8px 0;
}

.set-deadline-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px var(--accent-shadow-hover);
  border-color: var(--accent-border-focus);
}

.notes-section {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid var(--accent-border);
}

.notes-hint {
  margin-top: 8px;
  font-size: 14px;
  color: var(--text-color);
  opacity: 0.6;
  font-style: italic;
}

.modal-buttons {
  display: flex;
  gap: 12px;
  margin-top: 20px;
}

.modal-buttons button {
  flex: 1;
  margin-top: 0;
}

.cancel-btn {
  background: linear-gradient(60deg, rgba(255, 107, 107, 0.3) 0%, rgba(255, 107, 107, 0.5) 100%) !important;
  border-color: rgba(255, 107, 107, 0.4) !important;
}

.cancel-btn:hover {
  box-shadow: 0 15px 40px rgba(255, 107, 107, 0.5) !important;
  border-color: rgba(255, 107, 107, 0.6) !important;
}

.resources-section {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid var(--accent-border);
}

.resources-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.resources-header h4 {
  margin: 0;
  color: rgba(99, 102, 241, 0.3);
  font-size: 18px;
  font-weight: 600;
}

.load-resources-btn {
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  border: 2px solid var(--accent-border-hover);
  border-radius: 20px;
  background: linear-gradient(60deg, var(--accent-bg) 0%, var(--accent-text) 100%);
  opacity: 0.8;
  color: white;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  margin-top: 0;
}

.load-resources-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px var(--accent-shadow-hover);
  border-color: var(--accent-border-focus);
}

.load-resources-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.resources-loading {
  text-align: center;
  padding: 20px;
  color: var(--text-color);
  opacity: 0.7;
  font-size: 14px;
}

.resources-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.resource-item {
  padding: 16px;
  background: var(--back-color);
  border: 1px solid var(--accent-border);
  border-radius: 12px;
  transition: all 0.3s ease;
}

.resource-item:hover {
  background: var(--box-color);
  border-color: var(--accent-border-hover);
  transform: translateX(4px);
}

.resource-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  color: var(--text-color);
  margin-bottom: 8px;
}

.resource-link:hover {
  color: var(--accent-text);
}

.resource-title {
  font-weight: 600;
  font-size: 16px;
  flex: 1;
}

.resource-stars {
  font-size: 14px;
  color: var(--text-color);
  opacity: 0.7;
  margin-left: 12px;
}

.resource-description {
  color: var(--text-color);
  opacity: 0.7;
  font-size: 14px;
  line-height: 1.5;
  margin: 8px 0;
}

.resource-type {
  display: inline-block;
  padding: 4px 12px;
  background: var(--accent-bg);
  border-radius: 12px;
  font-size: 12px;
  color: var(--accent-text);
  text-transform: capitalize;
}

.no-resources {
  text-align: center;
  color: var(--text-color);
  opacity: 0.5;
  font-style: italic;
  padding: 20px;
}

@media (max-width: 768px) {
  .modal-window {
    width: 95%;
    max-height: 90vh;
  }

  .modal-header {
    padding: 16px 20px;
  }

  .modal-header h2 {
    font-size: 1.3rem;
  }

  .modal-content {
    padding: 20px;
  }
}

