.statistics-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.statistics-page h1 {
  text-align: center;
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 40px;
  color: rgba(99, 102, 241, 0.3);
  background: transparent;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 24px;
  margin-bottom: 40px;
}

.stat-card {
  padding: 28px;
  background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
              0 0 20px rgba(33, 165, 185, 0.2),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(33, 165, 185, 0.2);
  text-align: center;
  transition: all 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(33, 165, 185, 0.4);
}

.stat-card h3 {
  margin: 0 0 20px 0;
  color: transparent;
  font-size: 20px;
  font-weight: 600;
  background: linear-gradient(135deg, #21a5b9 0%, #758228 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.stat-number {
  font-size: 48px;
  font-weight: bold;
  color: #ffffff;
  margin: 10px 0;
  background: linear-gradient(135deg, #fff 0%, #21a5b9 50%, #758228 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.stat-percentage {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 10px;
}

.stat-card.completed {
  border-color: rgba(117, 130, 40, 0.4);
}

.stat-card.in-progress {
  border-color: rgba(33, 165, 185, 0.4);
}

.stat-card.not-started {
  border-color: rgba(255, 107, 107, 0.4);
}

.progress-circle-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}

.progress-circle {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: conic-gradient(
    rgba(117, 130, 40, 0.8) 0%,
    rgba(117, 130, 40, 0.8) calc(var(--progress) * 1%),
    rgba(33, 165, 185, 0.2) calc(var(--progress) * 1%),
    rgba(33, 165, 185, 0.2) 100%
  );
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  box-shadow: 0 0 30px rgba(117, 130, 40, 0.5);
}

.progress-circle::before {
  content: '';
  position: absolute;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: linear-gradient(135deg, rgba(26, 0, 55, 0.5) 0%, rgba(0, 0, 0, 0.7) 100%);
}

.progress-value {
  position: relative;
  z-index: 1;
  font-size: 32px;
  font-weight: bold;
  color: #ffffff;
}

.progress-chart {
  margin-top: 40px;
  padding: 28px;
  background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
              0 0 20px rgba(33, 165, 185, 0.2),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(33, 165, 185, 0.2);
}

.progress-chart h2 {
  margin: 0 0 30px 0;
  color: rgba(99, 102, 241, 0.3);
  text-align: center;
  font-size: 28px;
}

.chart-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.chart-bar {
  display: flex;
  align-items: center;
  gap: 20px;
}

.chart-label {
  min-width: 120px;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
  font-size: 16px;
}

.chart-bar-container {
  flex: 1;
  height: 40px;
  background: rgba(26, 0, 55, 0.4);
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
}

.chart-bar-fill {
  height: 100%;
  border-radius: 20px;
  transition: width 0.5s ease;
  position: relative;
}

.completed-fill {
  background: linear-gradient(90deg, rgba(117, 130, 40, 0.8) 0%, rgba(117, 130, 40, 1) 100%);
}

.in-progress-fill {
  background: linear-gradient(90deg, rgba(33, 165, 185, 0.8) 0%, rgba(33, 165, 185, 1) 100%);
}

.not-started-fill {
  background: linear-gradient(90deg, rgba(255, 107, 107, 0.8) 0%, rgba(255, 107, 107, 1) 100%);
}

.chart-value {
  position: absolute;
  right: 15px;
  color: rgba(255, 255, 255, 0.9);
  font-weight: bold;
  font-size: 16px;
  z-index: 1;
}

.empty-state {
  text-align: center;
  padding: 60px 20px;
  color: rgba(255, 255, 255, 0.7);
}

.empty-state p {
  font-size: 18px;
  margin: 10px 0;
}

@media (max-width: 768px) {
  .stats-grid {
    grid-template-columns: 1fr;
  }

  .chart-bar {
    flex-direction: column;
    align-items: flex-start;
  }

  .chart-label {
    min-width: auto;
  }
}

